@charset "utf-8";

.home_section
{
    margin: 0 auto;
    width: 100%;
    height: auto;
    background-color: rgb(15, 15, 15);
}

.home_section > .intro_area
{
    width: 100%;
    height: 950px;
    position: relative;
}

.intro_area > .intro_slide
{
    width: 100%;
    height: 100%;
    position: relative;
}

.intro_slide > li
{
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: -moz-radial-gradient(center, ellipse cover,  rgba(0,0,0,0) 50%, rgba(0,0,0,0.65) 100%); /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover,  rgba(0,0,0,0) 50%,rgba(0,0,0,0.65) 100%); /* Chrome10-25,Safari5.1-6 */
    background: radial-gradient(ellipse at center,  rgba(0,0,0,0) 35%,rgba(0,0,0,0.65) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.intro_slide > li:after
{
    content: '';
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 100%;
    background: -moz-radial-gradient(center, ellipse cover,  rgba(0,0,0,0) 50%, rgba(0,0,0,0.65) 100%); /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover,  rgba(0,0,0,0) 50%,rgba(0,0,0,0.65) 100%); /* Chrome10-25,Safari5.1-6 */
    background: radial-gradient(ellipse at center,  rgba(0,0,0,0) 35%,rgba(0,0,0,0.65) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

.intro_slide > li > img
{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    display: block;
    margin: 0 auto;
    width: 1980px;
    height: 100%;
}

.intro_slide > li > .slide_title
{
    position: absolute;
    z-index: 1;
    top:25%;
    left:9.5%;
    width: 50%;
    height: auto;
    color: #fff;
    font-size: 5em;
}

.slide_title > span:nth-child(1)
{
    display: block;
    width: 100%;
    border-left: 3px solid #ffd800;
    padding-left: 2%;
}

.slide_title > span:nth-child(2)
{
    display: block;
    width: 100%;
    font-size: 0.5em;
}

.slide_title > .link_button
{
    margin-top: 2%;
    width: 65px;
    height: 65px;
    border-radius: 5px;
    background-color:#9da15a;
    background-image: url("../img/playbt.png");
    background-size: contain;
    background-position: center;
    transition-duration: 0.1s;
    cursor: pointer;
    border: none;
}

.link_button:hover
{
    background-color: #fff;
}


.intro_area > .slide_select_list
{
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: 5%;
    left:50%;
    transform: translate(-50%,0%);
    width: 80%;
    height: 250px;
}

.slide_select_list > li
{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: 0% 1% 0% 1%;
    width: 25%;
    min-width: 200px;
    height: 100%;
}

.slide_select_list > li > .thumbnail
{
    position: relative;
    width: 100%;
    height: auto;
    border-radius: 5px;
    overflow: hidden;
}

.thumbnail::after
{
    content: '';
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 0 2px rgba(255, 216, 0 ,0.5) inset;
}

.thumbnail > img
{
    width: 100%;
    height: auto;
    transition-duration: 0.3s;
}

.thumbnail:hover > img
{
    transform: scale(1.07);
}

.slide_select_list > li > .thumbnail_text
{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 25%;
    box-sizing: border-box;
    padding-top: 1%;
    font-size: 19px;
    color: #fff;
}

.home_section > .news_area
{
    width: 100%;
    height: 1080px;
    background: -moz-radial-gradient(center, ellipse cover,  rgb(255, 255, 255) 50%, rgb(119, 98, 73) 100%); /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover,  rgb(255, 255, 255) 50%,rgb(119, 98, 73) 100%); /* Chrome10-25,Safari5.1-6 */
    background: radial-gradient(ellipse at center,  rgb(255, 255, 255) 35%,rgb(119, 98, 73) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    position: relative;
    box-sizing: border-box;
    padding-top: 5%;
    padding-left : 9.5%;
    padding-right: 9.5%;
}

.news_area > .news_area_title
{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    font-size: 3em;
    /* border-left: 3px solid #ffd800;
    padding-left: 2%; */
}

.news_area_title > span:nth-child(1)
{
    border-left: 3px solid #ffd800;
    padding-left: 2%;
    color: #fff;
}

.news_area_title > span:nth-child(2)
{
    width: auto;
    margin-top: 15px;
    font-size: 21px;
}

.news_area_title > span:nth-child(2) > a
{
    padding: 5px 10px 5px 10px;
    width: auto;
    height: 50px;
    border-radius: 5px;
    background-color: #ffd800;
    color: #000;
}

.news_area_title > span:nth-child(2) > a:hover
{
    background-color: white;
}

.news_area > .news_slide
{
    margin-top: 5%;
    width: 100%;
    height: 650px;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
}

.news_slide > .news_summary
{
    position: absolute;
    top: 0px;
    left:0px;
    width: 100%;
    height: 650px;
    box-sizing: border-box;
    padding: 15px 15px 0% 15px;
    background-color: rgba(0, 0, 0,0.5);
    color: #fff;
}

.news_summary > img
{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    display: block;
    margin: 0 auto;
    width: 1920px;
    height: 100%;
}

.news_summary > .news_head_title
{
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 45px;
    background-color: rgb(0, 0, 0,0.3);
    padding: 10px 10px 0px 10px;
}

.news_head_title > h1
{
    height: auto;
    border-left: 3px solid #ffd800;
    padding-left: 10px;
}


.news_summary > .news_category_title
{
    position: absolute;
    bottom: 0px;
    left:0px;
    width: 100%;
    height: 150px;
    box-sizing: border-box;
    overflow: hidden;
    padding: 10px 10px 0px 10px;
    background-color: rgb(0, 0, 0,0.5);
}

.news_category_title > h2
{
    height: auto;
    border-left: 3px solid #ffd800;
    padding-left: 10px;
}

.news_category_title > p
{
    margin-top: 15px;
    display: block;
    width: 100%;
    height: auto;
    font-size: 18px;
}

.news_area > .news_select
{
    margin: 0 auto;
    margin-top: 15px;
    display: flex;
    justify-content: space-around;
    width: 300px;
    height: 30px;
}

.news_select > li
{
    width: 30px;
    height: 30px;
    border-radius: 100%;
    cursor: pointer;
    box-shadow: 0 0 0 2px rgb(0, 0, 0) inset ;
}

.home_section > .store_door
{
    width: 100%;
    height: 950px;
    position: relative;
    background-color: #191919;
    position: relative;
    box-sizing: border-box;
    padding-top: 7%;
    padding-left : 9.5%;
    padding-right: 9.5%;
    overflow: hidden;
}

.store_door::after
{
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 100%;
    content: '';
    opacity: 0.2;
    background-repeat: no-repeat;
    background-image: url(../img/Storeimg.jpg);
    background-size: cover;
    background-position: center;
    filter: blue(5px);
    -webkit-filter: blur(3px);
}

.store_door > .store_inner
{
    position: absolute;
    top:50%;
    left:50%;
    z-index: 1;
    transform: translate(-50%,-50%);
    margin: 0 auto;
    width: 50%;
    height: auto;
}

.store_inner > img
{
    display: block;
    margin: 0 auto;
    width: 320px;
    height: auto;
}

.store_inner > span
{
    margin-top: 20px;
    display: block;
    text-align: center;
    color: #fff;
    font-size: 2em;
    font-weight: bold;
}

.store_inner > span > p
{
    font-size: 1.5rem;
}

.store_inner > button
{
    display: block;
    margin: 0 auto;
    margin-top: 25px;
    width: 160px;
    height: 50px;
    font-size: 1.5em;
    border: none;
    box-shadow: 0 0 0 3px #ffd800;
    font-weight: bold;
}


@media (min-width: 1920px)
{
    .home_section
    {
        width: 1920px;
    }
}

/*테블릿*/
@media (min-width: 767px) and (max-width: 1199px)
{
    .intro_slide > li > .slide_title
    {
        width: 80%;
        left:5.1%;
    }
    .slide_select_list > li
    {
        margin: 0% 5px 0% 5px;
        min-width: 180px;
    }

}

/*모바일*/
@media (max-width: 766px)
{
    .home_section > .intro_area
    {
        height: 850px;
    }
    .intro_slide > li > .slide_title
    {
        width: 80%;
        font-size: 3em;
    }
    .slide_title > span:nth-child(1)
    {
        text-align: center;
        border: none;
    }
    .slide_title > span:nth-child(2)
    {
        margin-bottom: 10px;
        text-align: center;
    }
    .slide_title > .link_button
    {
        display: block;
        margin: 0 auto;
        width: 100px;
        height: 55px;
        background-repeat: no-repeat;
        box-shadow: 3px 3px 3px 1px #191919;
    }
    .intro_area > .slide_select_list
    {
        width: 95%;
        flex-wrap: wrap;
        height: auto;
    }
    .slide_select_list > li
    {
        min-width: 175px;
    }
    .slide_select_list > li > span > p
    {
        font-size: 15px;
    }
    .home_section > .news_area
    {
        height: 800px;
    }
    .news_slide > .news_summary
    {
        height: 580px;
    }
    .home_section > .store_door
    {
        height: 500px;
    }
    .store_door > .store_inner
    {
        width: 100%;
    }
    .store_inner > img
    {
        width: 250px;
    }

}

/*최소*/
@media (max-width: 385px) 
{
    .intro_area > .slide_select_list
    {
        bottom: 10%;
    }
    .slide_select_list > li
    {
        width: 70%;
    }
    .slide_select_list > li > .thumbnail
    {
        display: none;
    }
    .slide_select_list > li > .thumbnail_text
    {
        margin-bottom: 10px;
        background-color: rgb(116, 98, 79,0.8);
        font-size: 15px;
        text-align: center;
        box-sizing: border-box;
        padding: 10px;
        border-radius: 5px;
    }
    .slide_select_list > li > .thumbnail_text:hover
    {
        color: #000;
        background-color: #ffd800;
    }
}