@charset "utf-8";

.portfolio_section
{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    min-height: 1000px;
    background-color: #d8e7f9;
    font-family: 'GmarketSansMedium';
}

.portfolio_section > .portfolio_box
{
    margin: 0 auto;
    margin-top: 100px;
    margin-bottom: 7%;
    width: 300px;
    height: auto;
    min-height: 150px;
    background-color: #fff;
    border-radius: 10px;
    background-image: url(../imgs/bg.png);
    box-shadow: 0 0 30px 30px rgba(185, 180, 255,0.5);
    transition-duration: 1s;
    overflow: hidden;
}

.portfolio_box > .portfolio_inner
{
    display: none;
    width: 100%;
    height: 100%;
    padding: 2%;
    box-sizing: border-box;
}

.portfolio_inner> .port_title
{
    display: block;
    width: 100%;
    height: auto;
    font-size: 35px;
    margin-bottom: 2%;
    text-align: center;
    padding-bottom: 1%;
    border-bottom: 2px solid rgb(255, 174, 120);
}

.portfolio_inner > .portfolio_list
{
    margin-bottom: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.portfolio_list > li
{
    margin: 2% 2% 2% 0%;
    width: 300px;
    height: auto;
    overflow: hidden;
    color: #fff;
}

.portfolio_list > li > .list_info
{
    margin-top: 20px;
    display: block;
    width: 100%;
    height: auto;
    text-align: center;
    font-size: 18px;
    color: #191919;
}

.list_info > p
{
    margin-bottom: 10px;
}

.portfolio_list > li > a
{
    margin: 0 auto;
    display: block;
    width: 300px;
    height: 300px;
    border-radius: 100px;
    overflow: hidden;
    color: #fff;
    position: relative;
}

.portfolio_list > li > a:hover > img
{
    transform: scale(1.05);
}

.portfolio_list > li > a > img
{
    width: 100%;
    height: auto;
    transition-duration: 0.3s;
}
@media (max-width: 766px) 
{
    .portfolio_list > li
    {
        width: 100%;
        height: auto;
    }
    .portfolio_list > li > a
    {
        width: 100%;
        height: auto;
        box-sizing: border-box;
        padding: 10px;
    }
    .portfolio_list > li > a > img
    {
        display: block;
        margin: 0 auto;
        width: 80%;
        height: 80%;
        border-radius: 100px;
    }
    .portfolio_list > li > .list_info
    {
        margin-top: 25px;
    }
    .portfolio_inner> .port_title
    {
        font-size: 8vw;
    }

}