@charset "utf-8";

.showcase_section
{
    position: relative;
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    background-color: rgb(226, 226, 226);
    overflow: hidden;
}

.showcase_section > .showcase_menu
{
    position: absolute;
    top:0px;
    left:0px;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 220px;
    height: 100%;
    background-color: rgba(0, 0, 0,0.5);
}

.showcase_menu > .to_main
{
    width: 100%;
    height: 175px;
    background-color: rgb(255, 255, 255);
    color: #fff;
}

.to_main > a
{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
}

.to_main > a > img
{
    margin: 0 auto;
    width: auto;
    height: 100px;
}
.to_main > a > span
{
    width: 100%;
    text-align: center;
    font-size: 14px;
}

.showcase_menu > .showcase_list
{
    width: 100%;
    height: auto;
}

.showcase_list > li
{
    position: relative;
    width: 100%;
    height: 120px;
    box-sizing: border-box;
    padding: 15px;
    background-color: rgb(25, 25, 25,0.5);
    transition-duration: 0.1s;
    color: #fff;
    cursor: pointer;
}

.showcase_list > li:hover
{
    background-color: rgba(255, 255, 255,0.5);
    color: #000;
}
.showcase_list > li > span
{
    display: block;
    margin-bottom: 15px;
    width: 100%;
    height: auto;
}

.showcase_list > li > span:nth-child(2)
{
    font-weight: 600;
}

.showcase_menu > .sns_list
{
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 50px;
    box-sizing: border-box;
    padding: 0px 10px 0px 10px;
}

.sns_list > li
{
    width: 50px;
    height: 100%;
}

.sns_list > li > a
{
    display: block;
    width: 100%;
    height: 100%;
}

.sns_list > li > a > img
{
    width: 100%;
    height: 100%;
}

.showcase_menu > .download
{
    margin-top: 10px;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    padding: 0px 10px 0px 10px;
}

.download > span
{
    margin-bottom: 10px;
    display: block;
    position: relative;
    width: 100%;
    height: 65px;
    overflow: hidden;
    border-radius: 10px;
    background-color: #696969;
}

.download > span > img
{
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    width: 85%;
    height: 85%;
}

.showcase_section > .slide_section
{
    position: absolute;
    top:0px;
    left:0px;
    width: 100%;
    height: 100%;
    background-color: gold;
}



